/* Style for the dialog */
dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 300px;
    max-width: 600px;
    background-color: var(--color-light);
    border: 1px solid var(--color-gray-light);
    border-radius: 4px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 1;
    
    
   
    
}
.modal-text{
    word-wrap: break-word; /* Break long words */
  overflow-wrap: break-word; /* Ensure overflow wraps */
}




   
  


dialog[open] {
    -webkit-animation: show 0.3s ease normal;
}
dialog.hide {
    -webkit-animation: hide 0.3s ease normal;
}
@-webkit-keyframes show{
    from {
        opacity: 0;
        scale: 0.9;
    }
    to {
        opacity:1;
        scale: 1;
    }
}
@-webkit-keyframes hide{
    to {
        opacity: 0;
        scale: 0.9;
    }
}


/* Style for the close button */
.close-modal {
    background-color: var(--color-accent);
    color:var(--color-primary);
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    
}


#new-task-form {
    
    border: none;
    padding: 10px;
    color: var(--color-gray);
    font-weight: ;
    font-size: 0.9rem;
    border-radius: 5px;
    cursor: pointer;
    background-color: var(--color-gray-light);
    
    
}
#new-task-form::before{
    content: '+';
    font-weight: bold;
    margin-right: 5px;

}

.close-modal:hover {
    
}


#task-done{
    border-right: 5px solid var(--color-green);
}
#task-not-done{
    border-right: 5px solid var(--color-delete);
}






#edit-task{
    font-weight: bold;
    margin-bottom: 20px;
}






#delete-list{
    color: var(--color-dark);
    
    
    right: 20px;
    cursor: pointer;
    display: none;
    font-size: 1rem; 
    font-weight: bold;
    
    
}

.list-container{
    
    cursor: pointer;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    transition: background-color 100ms ease;
    word-break: break-word;
    overflow-wrap: break-word; 
}   
.list-container:hover{
    background-color:var(--color-gray-light);
    
}

.active-list{
    background-color: var(--color-gray-light);
    padding: 10px 20px;
}



@media (max-width: 900px) {

    .add-list-btn{
        font-size: 0.7rem;
    ;
    }
    .container{
        padding: 0px;
        display: block;
       
    }
    aside{
        
        width: 40px;
        height: 100vh;
        border-radius: 0px;
        transition: width 0.3s ease-in-out;
    }
    section{
        margin-left: 40px;
        padding-left: 20px;
       
    }
    aside div,
aside h1 {
    display: none;
    opacity: 0; /* Start with opacity 0 */
    transition: opacity 1s;
     /* Define transition property */
}
    
    aside:hover{
        width: 200px;
        z-index: 1;
    }
    aside:hover div , aside:hover h1{
        display: block;
        transition: display 1s;
        
        opacity: 1;
    }
    .new-list-name{
        font-size: 0.8rem;
    }
 
    
}





@media (max-width: 500px) {

.tasks-container{
    grid-template-columns: repeat(auto-fit,minmax(200px , 1fr));
}






