n    display: flex;\n    flex-direction: column;\n    width:  300px;\n    overflow-x: hidden;\n    transition: width 200ms ease;\n    position: fixed;\n    \n}\n\n\n\n.lists-header {\n    font-size: 24px;\n    font-weight: bold;\n    margin-bottom: 20px;\n    color: var(--color-text);\n    \n  \n    \n}\n\n\n\n\n.lists-container {\n   margin-bottom: 20px;\n    \n    list-style: none;\n    \n\n}\n.lists-container li{\n    padding: 10px;\n}\n\n\n\n.add-list-btn {\n    \n    border-radius: 5px;\n   padding: 10px 50px ;\n   padding-left: 43px;\n    color: var(--color-light);\n    font-size: 0.9rem;\n    ;\n    background-color: var(--color-primary);\n    display: flex;\n    gap: 10px;\n    font-weight: bold;\n   align-items: center;\n    \n    border: none;\n    \n    cursor: pointer;\n    transition: 333ms cubic-bezier(.53, .00, .28, 1.00) background, 333ms cubic-bezier(.53, .00, .28, 1.00) opacity;\n}\n\n.add-list-btn::before{\n    content: '+';\n   \n}\n\n\n#plus{\n    color: var(--color-light);\n    font-weight: bold;\n    font-size: 1.2rem;\n    align-self: flex-start;\n    \n    padding-bottom: 3px;\n\n}\n\n.add-list-btn:hover {\n  background-color:#0052b6;}\n\n\n\n\n.new-list-name {\n    display: block;\n    padding: 8px;\n    margin-top: 15px;\n    max-width: 100%;\n    \n    border: 2px solid var(--color-gray-light);\n    border-radius: 7px ;\n    color: var(--color-dark);\n    outline: none;\n    font-size: 1.1rem;\n\n   \n   \n}\n\n    \n\n.new-list-name.invalid-input{\n    \n    border: 1px solid;\n    border-color: var(--color-error);\n }\n .listname-error{\n    color: var(--color-error);\n    font-size: 1rem;\n    \n }\n\n\n.del-lists {\n    \n    padding: 20px 10px;\n    background-color: var(--color-accent);\n    \n    border: none;\n    \n    \n    \n    \n    font-size: 1rem;\n   \n\n    \n    \n    color: var(--color-gray);\n    margin-top: auto;\n}\n\n.del-lists:hover {\n color: var(--color-delete);\n}\n\n\n.list-header {\n    font-size: 1.5rem;\n    font-weight: bold;\n    margin-bottom: 40px;\n    color: var(--color-dark);\n    \n}\n\n\n\n\n\n.create-task-form {\n    margin-bottom: 20px;\n    \n}\n\n\n\n.create-task-form input[type=\"text\"] {\n    width: 100%;\n    padding: 8px;\n    border: none;\n    border-radius: 4px;\n    outline: none;\n    margin-bottom: 10px;\n    border: 1px solid var(--color-gray-light);\n    \n    \n}\n.create-task-form input[type=\"text\"]:focus  {\n    border: 1px solid var(--color-primary);\n    \n    }\n\n.taskform-label{\n    padding: 10px 0px;\n    font-weight: bold;\n    \n}\n\n.create-task-form input[type=\"date\"]{\n    padding: 10px  ;\n    border: none;\n    border-radius: 5px;\n    outline: none;\n}\n\n.create-task-form input[type=\"radio\"] {\n    margin-right: 5px;\n}\n\n.create-task-form label {\n    margin-right: 15px;\n}\n\n\n.taskform-btn{\n    margin-top: 20px;\n    background-color: var(--color-light);\n    border: none;\n    padding: 10px;\n    color: var(--color-primary);\n    font-weight: ;\n    font-size: 0.9rem;\n    border-radius: 5px;\n    cursor: pointer;\n    \n}\n.taskform-btn:hover,#new-task-form:hover{\n    \n    color: var(--color-dark);\n    background-color: var();\n}\n\n.task-container .task-info{\n    display: flex;\n    gap: 10px;\n    align-items: center;\n    justify-content: center;\n   \n}\n.task-container {\n    list-style: none;\n    \n    padding: 10px;\n    background-color: var(--color-light);\n    box-shadow: 10px 8px 5px rgba(0, 0, 0, 0.2);\n    border-radius: 4px;\n    margin-bottom: 20px;\n    \n    \n\n   \n}\n\n\n\n\n\n.task-container .task-title {\n    \n    font-size: 1rem;\n    font-weight: bold;\n    display: flex;\n    flex-wrap: wrap;\n   justify-content: center;\n   word-break: break-word;\n   overflow-wrap: break-word; \n}\n\n\n.task-container .task-wrapper{\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n\n.task-container .task-date {\n    font-size: 14px;\n    color: #666;\n    color: var(--color-gray);\n}\n\n.task-container button {\n    \n    font-size: 0.8rem;\n   \n    \n    \n    border: none;\n   \n    background-color: var(--color-light);\n    color: var(--color-primary);\n    cursor: pointer;\n}\n\n.task-container button:hover {\n    color:#86acdf;\n    \n}\n.task-container #priority{\n    color: var(--color-gray);\n    \n}\n\n.task-container .delete-task-btn{\n    color: var(--color-delete);\n}\n.delete-task-btn:hover{\n    color:#f77d6f !important;\n}\n\n#is-done-btn{\n   \n    margin-right: 5px;\n    \n    \n\n    \n}\n\n\n\n.task-container .task-info{\ndisplay: flex;\nflex-wrap: wrap;\n  \n    \n}\n.task-container .task-actions{\n    margin-top: 20px;\n    display: flex;\n    justify-content: center;\n    gap: 10px;\n}\n\n.task-title{\n    display: flex;\n    align-items: center;\n}\n\n\n\n\n/* Style for the dialog */\ndialog {\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    min-width: 300px;\n    max-width: 600px;\n    background-color: var(--color-light);\n    border: 1px solid var(--color-gray-light);\n    border-radius: 4px;\n    padding: 20px;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\n    z-index: 1;\n    \n    \n   \n    \n}\n.modal-text{\n    word-wrap: break-word; /* Break long words */\n  overflow-wrap: break-word; /* Ensure overflow wraps */\n}\n\n\n\n\n   \n  \n\n\ndialog[open] {\n    -webkit-animation: show 0.3s ease normal;\n}\ndialog.hide {\n    -webkit-animation: hide 0.3s ease normal;\n}\n@-webkit-keyframes show{\n    from {\n        opacity: 0;\n        scale: 0.9;\n    }\n    to {\n        opacity:1;\n        scale: 1;\n    }\n}\n@-webkit-keyframes hide{\n    to {\n        opacity: 0;\n        scale: 0.9;\n    }\n}\n\n\n/* Style for the close button */\n.close-modal {\n    background-color: var(--color-accent);\n    color:var(--color-primary);\n    border: none;\n    padding: 10px 20px;\n    font-size: 16px;\n    border-radius: 5px;\n    cursor: pointer;\n    margin-top: 10px;\n    \n}\n\n\n#new-task-form {\n    \n    border: none;\n    padding: 10px;\n    color: var(--color-gray);\n    font-weight: ;\n    font-size: 0.9rem;\n    border-radius: 5px;\n    cursor: pointer;\n    background-color: var(--color-gray-light);\n    \n    \n}\n#new-task-form::before{\n    content: '+';\n    font-weight: bold;\n    margin-right: 5px;\n\n}\n\n.close-modal:hover {\n    \n}\n\n\n#task-done{\n    border-right: 5px solid var(--color-green);\n}\n#task-not-done{\n    border-right: 5px solid var(--color-delete);\n}\n\n\n\n\n\n\n#edit-task{\n    font-weight: bold;\n    margin-bottom: 20px;\n}\n\n\n\n\n\n\n#delete-list{\n    color: var(--color-dark);\n    \n    \n    right: 20px;\n    cursor: pointer;\n    display: none;\n    font-size: 1rem; \n    font-weight: bold;\n    \n    \n}\n\n.list-container{\n    \n    cursor: pointer;\n    border-radius: 5px;\n    display: flex;\n    justify-content: space-between;\n    transition: background-color 100ms ease;\n    word-break: break-word;\n    overflow-wrap: break-word; \n}   \n.list-container:hover{\n    background-color:var(--color-gray-light);\n    \n}\n\n.active-list{\n    background-color: var(--color-gray-light);\n    padding: 10px 20px;\n}\n\n\n\n@media (max-width: 900px) {\n\n    .add-list-btn{\n        font-size: 0.7rem;\n    ;\n    }\n    .container{\n        padding: 0px;\n        display: block;\n       \n    }\n    aside{\n        \n        width: 40px;\n        height: 100vh;\n        border-radius: 0px;\n        transition: width 0.3s ease-in-out;\n    }\n    section{\n        margin-left: 40px;\n        padding-left: 20px;\n       \n    }\n    aside div,\naside h1 {\n    display: none;\n    opacity: 0; /* Start with opacity 0 */\n    transition: opacity 1s;\n     /* Define transition property */\n}\n    \n    aside:hover{\n        width: 200px;\n        z-index: 1;\n    }\n    aside:hover div , aside:hover h1{\n        display: block;\n        transition: display 1s;\n        \n        opacity: 1;\n    }\n    .new-list-name{\n        font-size: 0.8rem;\n    }\n \n    \n}\n\n\n\n\n\n@media (max-width: 500px) {\n\n.tasks-container{\n    grid-template-columns: repeat(auto-fit,minmax(200px , 1fr));\n}\n\n\n\n\n\n\n}"],"sourceRoot":""}]);
